html {
  font-size: 56.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  padding: 0;
  margin: 0;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

#boton-top {
  display: none; /* Inicialmente oculto */
  position: fixed;
  bottom: 10rem;
  right: 4rem;
  padding: 1rem;
  font-size: 3rem;
  width: 5.5rem;
  height: 5.5rem;
  background-color: rgba(0, 255, 255, 0.226);
  color: #fff;
  border: none;
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2823529412);
  transition: all 0.5s;
  cursor: pointer;
}

#boton-top:hover {
  background-color: rgb(0, 195, 255);
}

#btnWhatsApp {
  display: block; /* O elige c√≥mo deseas mostrarlo inicialmente */
  position: fixed;
  bottom: 4rem;
  right: 4rem;
  width: 5.5rem;
  height: 5.5rem;
  padding: 1rem;
  font-size: 2.7rem;
  background-color: #3ec05c;
  color: #fff;
  border: none;
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2823529412);
  cursor: pointer;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/* UI */
.contenedor-footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: #17a2b8; /* Asigna el color de fondo oscuro desde la variable $gray-dark */
  color: #fff; /* Color de texto blanco */
  padding: 5rem;
}
.contenedor-footer .contenedor-gr {
  display: inline-table;
  padding: 10rem;
  padding-bottom: 0;
  padding-top: 1.5rem;
}
@media (max-width: 935px) {
  .contenedor-footer .contenedor-gr {
    display: block;
    width: 100%;
    text-align: center;
    padding: 0;
    margin-bottom: 4rem;
  }
}
.contenedor-footer p {
  color: #fff;
  font-size: 1.8rem;
  margin: 0;
  padding-bottom: 2rem;
}
.contenedor-footer .redes a {
  display: block;
  padding-left: 3.5rem;
  color: #17a2b8;
  padding: 1rem;
  margin: 0 auto;
  margin-bottom: 0.7rem;
  font-size: 2.3rem;
  transition: all 0.7s;
  width: 4.3rem;
  height: 4.3rem;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.262745098);
}
@media (max-width: 935px) {
  .contenedor-footer .redes a {
    display: inline-block;
    margin-right: 1rem;
    padding-left: 1rem;
    width: 4.5rem;
    height: 4.5rem;
    font-size: 2.4rem;
  }
}
.contenedor-footer .copyright {
  margin-top: 4rem;
  font-size: 1.4rem; /* Ajusta el tama√±o de fuente seg√∫n sea necesario */
}

header {
  padding: 1rem 0;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.116);
  position: fixed;
  width: 100%;
  height: auto;
  margin: auto;
  padding: auto;
  z-index: 1; /* Asegurar que est√© encima de otros elementos */
}
header .contenedor-redes {
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(83, 83, 83, 0.231372549);
  padding-bottom: 0.5rem;
}
header .contenedor-redes .redes-sociales {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}
header .contenedor-redes .redes-sociales li {
  margin-right: 0.5rem;
  padding-top: 0.5rem;
}
header .contenedor-redes .redes-sociales li a {
  text-decoration: none;
  color: #17a2b8;
  font-size: 2rem;
  margin-left: 1.5rem;
}
header .contenedor-redes h2 {
  font-size: 1.5rem;
  margin: 0;
  margin-right: 1.5rem;
  padding-top: 0.5rem;
}
header .contenedor-header {
  background-color: #ffffff;
  display: flex;
  flex-direction: column; /* Cambiar a columna para centrar verticalmente */
  justify-content: center; /* Centrar verticalmente */
  align-items: center; /* Centrar horizontalmente */
  height: auto;
}
header .contenedor-header .barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 8rem;
  max-width: 120rem; /* Ajustar el ancho m√°ximo seg√∫n tus necesidades */
}
header .contenedor-header .barra img {
  width: 15rem;
  height: auto;
  margin-right: 5rem;
  z-index: 1000;
}
header .contenedor-header .barra nav {
  display: flex;
}
header .contenedor-header .barra nav .navegacion {
  margin-left: 2rem;
}
header .contenedor-header .barra nav .navegacion a {
  color: #17a2b8;
  margin-right: 3rem;
  transition: all 0.5s;
  font-size: 1.7rem;
}
header .contenedor-header .barra nav .navegacion a:hover {
  opacity: 0.5;
}
header .contenedor-header .barra nav .navegacion .submenu {
  position: relative;
  display: inline-block;
}
header .contenedor-header .barra nav .navegacion .submenu:hover .submenu-contenido {
  display: block;
}
header .contenedor-header .barra nav .navegacion .submenu .submenu-contenido {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: rgba(139, 139, 185, 0.8823529412);
  padding: 2rem;
  border-radius: 0.2rem;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.213);
  z-index: 1;
  width: auto;
  transition: all 0.5s;
}
header .contenedor-header .barra nav .navegacion .submenu .submenu-contenido a {
  width: auto;
  display: inline-block;
  color: #fff;
  padding: 1rem;
  transition: all 0.5s;
  border-bottom: 1px solid rgba(255, 255, 255, 0.037);
}
header .contenedor-header .barra nav .navegacion .submenu .submenu-contenido a:hover {
  opacity: 0.5;
}
header .contenedor-header .barra .boton a {
  padding: 1rem 2rem;
  font-size: 1.7rem;
  background-color: #17a2b8;
  color: #fff;
  text-decoration: none;
  border-radius: 5rem;
  transition: all 0.7s;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1607843137);
}
header .contenedor-header .barra .boton a:hover {
  background-color: #a5a7a9;
  color: #fff;
}
header .contenedor-header .barra .menu-icon {
  display: none;
  font-size: 2.3rem;
  cursor: pointer;
}
header .contenedor-header .barra .menu-icon i {
  color: #17a2b8;
  transition: color 0.5s ease-in;
}
@media (max-width: 935px) {
  header .contenedor-header .barra .menu-icon {
    display: block;
  }
}

@media (max-width: 935px) {
  header {
    background-color: #ffffff;
  }
  .contenedor-redes i {
    display: none;
  }
  .contenedor-redes h2 {
    display: none;
  }
  .contenedor-header .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .contenedor-header .barra img {
    width: 15rem;
    height: auto;
    z-index: 100;
  }
  .contenedor-header .barra .menu-icon {
    display: block;
    cursor: pointer;
    margin-left: auto;
    margin-right: 4.5rem;
  }
  .contenedor-header .barra .menu-icon i {
    padding-top: 0.5rem;
    font-size: 3rem;
  }
  .contenedor-header .barra .navegacion {
    background-color: #ffffff;
    display: none;
    padding: 1rem;
    border-radius: 0.1rem;
    position: absolute;
    top: 11.4rem;
    right: 0;
    width: 100%;
    height: auto;
    z-index: -1;
    animation: deslizarMenu 0.7s ease-in-out;
  }
  .contenedor-header .barra .navegacion a {
    padding-left: 1.5rem;
    height: 2.7rem;
    border-bottom: 1px solid rgba(0, 255, 255, 0.229);
    color: #17a2b8;
    margin-right: 0;
    margin-bottom: 1rem;
    display: block;
  }
  .contenedor-header .barra .navegacion.menu-abierto {
    display: block;
  }
  @keyframes deslizarMenu {
    from {
      opacity: 0;
      transform: translateY(-100%);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  .contenedor-header .boton {
    display: none;
  }
}
.contenedor-formulario {
  width: auto;
  margin: auto;
  padding: auto;
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 5rem;
}
@media (max-width: 935px) {
  .contenedor-formulario {
    grid-template-columns: auto;
  }
}
.contenedor-formulario img {
  border-radius: 0.1rem;
}
.contenedor-formulario h3 {
  padding: 1rem;
  color: rgba(0, 0, 0, 0.69);
}
.contenedor-formulario .formulario {
  background-color: rgb(255, 255, 255);
  border-radius: 0.2rem;
  width: 100%;
  padding-top: 2rem;
  padding-bottom: 2rem;
  text-align: center;
  flex: 1;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.115);
}
.contenedor-formulario .formulario input, .contenedor-formulario .formulario textarea {
  margin-top: 1.5rem;
  background-color: rgba(255, 255, 255, 0.396);
  width: 70%;
  padding: 1rem;
  margin-bottom: 1.5rem;
  border: 1px solid #a5a7a9;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
  resize: none;
}
.contenedor-formulario .formulario input::placeholder {
  color: rgb(0, 0, 0);
}
.contenedor-formulario .formulario input[type=submit] {
  background-color: #17a2b8;
  text-transform: uppercase;
  color: white;
  border-radius: 5rem;
  cursor: pointer;
  border: none;
  transition: all 0.7s;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3254901961);
}
.contenedor-formulario .formulario input[type=submit]:hover {
  background-color: #a5a7a9;
}
@media (max-width: 935px) {
  .contenedor-formulario .formulario {
    width: 100%;
  }
}

.contactar {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-columns: auto;
  /* h4 {
      font-size: 2.5rem;
      padding: 5rem;
      font-weight: $regular;
      text-align: left;
      position: absolute;
      top: 10%;
      left: 30%;
      transform: translate(-50%);
      color: $gray-dark;
      width: auto; // Ajusta seg√∫n tus necesidades
      cursor: pointer;
  } */
}
.contactar .contactar-img {
  background-image: url(../../build/img/attractive2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  cursor: pointer;
}
.contactar .contactar-imagen {
  background-image: url(../../build/img/ortodoncia2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  cursor: pointer;
}
.contactar p {
  background-color: #fff;
  border-radius: 0.2rem;
  font-size: 1.6rem;
  font-weight: 500;
  padding: 3rem;
  text-align: left;
  position: absolute;
  top: 30%;
  left: 30%;
  transform: translate(-50%);
  color: #343a40;
  width: auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.104);
  cursor: pointer;
}
.contactar button {
  border: none;
  background-color: #17a2b8;
  font-size: 1.5rem;
  padding: 0.7rem;
  text-transform: uppercase;
  font-weight: 400;
  text-align: left;
  position: absolute;
  top: 65%;
  left: 14%;
  transform: translate(-50%);
  color: #fff;
  width: auto;
  transition: all 0.7s;
  cursor: pointer;
}
@media (max-width: 935px) {
  .contactar {
    display: none;
  }
}

.slider {
  overflow: hidden;
  width: 100%;
}
.slider .img-slider {
  display: flex;
  transition: transform 0.5s ease;
}
.slider .img-slider img {
  width: 100%;
  height: 60rem;
  flex: 0 0 auto;
  transition: transform 0.5s ease;
}
@media (max-width: 935px) {
  .slider .img-slider {
    height: auto;
  }
}
@media (max-width: 480px) {
  .slider .img-slider {
    height: auto;
  }
}
.slider .prev-button, .slider .next-button {
  position: absolute;
  top: auto;
  transform: translateY(-50%);
  background: #17a2b8;
  border: none;
  cursor: pointer;
  padding: 1rem;
  z-index: 0;
}
.slider button {
  color: #fff;
}
.slider .prev-button {
  left: 1rem;
}
.slider .next-button {
  right: 1rem;
}

.contenedor-informacion {
  background-color: #fff;
  width: 85%;
  padding-top: 3rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
  border-radius: 0.1rem;
}
.contenedor-informacion h3 {
  margin: 0;
  padding-top: 2rem;
  color: rgba(0, 0, 0, 0.69);
}
.contenedor-informacion p {
  padding: 3.5rem;
  font-size: 1.5rem;
  text-align: center;
  margin: 0;
  color: rgba(0, 0, 0, 0.69);
}
.contenedor-informacion img {
  display: block;
  margin: 0 auto;
  max-width: 60%;
  height: 40rem;
  border-radius: 0.1rem;
}
.contenedor-informacion img {
  animation: ease-in animat 1s;
}
@keyframes animat {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.informacion {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  display: flex;
  justify-content: space-around;
  padding: 10rem;
  padding-top: 4rem;
  padding-bottom: 0;
}
.informacion .informacion-mv {
  background-color: #fff;
  border-radius: 0.2rem;
  max-width: 50rem;
  width: 80%;
  height: auto;
  padding: 4rem;
  padding-bottom: 0;
  transition: all 0.7s;
}
.informacion .informacion-mv h3 {
  color: rgba(0, 0, 0, 0.69);
  margin: 0;
  text-align: center;
}
.informacion .informacion-mv p {
  font-size: 1.5rem;
  color: rgba(0, 0, 0, 0.69);
  text-align: center;
}

.contenedor-equipo {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 5rem;
}
@media (max-width: 935px) {
  .contenedor-equipo {
    flex-direction: column;
  }
}
.contenedor-equipo .equipo {
  background-color: #fff;
  border-radius: 0.2rem;
  width: 80%;
  max-width: 35rem;
  height: auto;
  padding: 4rem;
  padding-bottom: 0;
  transition: all 0.7s;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.092);
}
.contenedor-equipo .equipo img {
  width: 50%;
  height: auto;
